import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.scss"

function Detail(props) {


    
    let [alert_active, changeAlert_active] = useState(true)




    useEffect(() => {
     let alert = setTimeout(() => { changeAlert_active(false) }, 2000)
     
     return () => {
       clearTimeout(alert);
     }
    }, [])


    let Box = styled.div`
      padding: 20px;
    `;


  

    let Title = styled.h4`
      font-size: 25px
    `;

    let { id } = useParams();
    let history = useHistory();
    let found_product = props.shoes.find(function(shoes) {
      return shoes.id == id
    })

    return(
      <div className="container">
      <Box>
        <Title className="red">Detail</Title>
      </Box>
      {
      alert_active === true ? <div className="my-alert2">
        <p>재고가 얼마 남지 않았습니다</p>
      </div>
      : <></>
      }
      
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes" + (parseInt(id) + 1) + ".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{found_product.title}</h4>
          <p>{found_product.content}</p>
          <p>{found_product.price}원</p>

          <Info left={props.left} shoes={props.shoes}></Info>
          
          <button style={{marginRight: "10px"}} className="btn btn-danger" onClick={() => {
            
              let newLeft = [...props.left];
              

          }}>주문하기</button>
          <button className="btn btn-danger" onClick={() => {
            history.push("/");
          }}>뒤로가기</button> 
        </div> 
      </div>
    </div> 
    )
  }

  
  


  function Info(props) {

    let newArray = props.left;
    let newShoes = props.shoes;
    let { id } = useParams();

    let found_product = newShoes.find(function(shoes) {
      return shoes.id == id
    })

    console.log(found_product);

    return (
    
    
   <p>재고 : {newArray[found_product.id]}</p>
  
     
    )

    

  }
  


  export default Detail;